/*REFERIDOS*/

.referidos--img-home { padding: 40px; background: #737373; }

.referidos--img-home h1, .referidos--img-home h2, .referidos--img-home h3 {
	margin: 0;
	padding: 0;
	color: white;
}

.referidos--img-home h3 {
	font-size: 1em;
}

.referidos--img-home h3.last-text {
	font-weight: 800;
}

.referidos--img-home h1 {
	font-weight: 800;
    padding: 5px 0;
}

.referidos--home { text-align: center; min-height: 330px; }

.referidos--home h1 {
	font-size: 1.7em;
}

.referidos--home ul { list-style: none; padding: 0; margin: 0; overflow: auto; width: 70%; margin: 0 auto; }
.referidos--home ul li { width: calc(100% / 4); float: left; }
.referidos--home ul li span { width: 40px; height: 40px; background: #b7b7b7; display: block; margin: 0 auto; }

.input-inline { padding: 0; padding-right: 15px; }
.input-inline:last-of-type { padding: 0; }
.link-sucursal { margin-top: 5%; }


@media (max-width: 992px){
	.input-inline { padding: 0; margin-top: 15px; }
	.link-sucursal { margin-top: 0; }
}